html, body {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    /*background: url("../img/bg.jpg");*/
    background: rgb(63, 94, 251);
    background: linear-gradient(90deg, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
    display: none
}

.App-header {
    width: 100vw;
    height: 8vh;
    display: flex;
    background-color: rgba(0, 0, 0, 0.2);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: PingFangSC-Medium;
    font-size: calc(10px + 2vmin);
    color: white;
    transform: scale(1);
    box-shadow: 0 2px 10px 4px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s, transform 0.5s;
    position: fixed;
    left: 0;
    top: 0;
}

.App-header:hover {
    transform: scale(1.3);
    box-shadow: 0 0 40px 8px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s, transform 0.5s;
}

.App-footer {
    width: 100vw;
    height: 6vh;
    display: flex;
    background-color: rgba(0, 0, 0, 0.2);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: white;
    position: fixed;
    left: 0;
    bottom: 0;
}

.poetry-box {
    width: 100vw;
    height: 86vh;
    margin-top: 8vh;
    padding: 50px 0;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: column;
    color: white;
    font-size: calc(8px + 2vmin);
    box-sizing: border-box;
}

.poetry-chapter {
    font-size: calc(12px + 2vmin);
}

.poetry-title {
    font-size: calc(9px + 2vmin);
    margin-top: 18px;
}

.poetry-content {
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 0 15px;
}

.poetry-content-item {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: calc(8px + 2vmin);
}
